*, :after, :before {
  box-sizing: border-box;
}

img {
  width: 100%;
  max-width: 100%;
  height: auto;
  display: block;
}

h1, h2, h3, h4, p {
  margin: 0;
  padding: 0;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  text-decoration: none;
}

button {
  cursor: pointer;
  border: none;
}

html, body {
  margin: 0;
  padding: 0;
  overflow: hidden;
}

#canvas {
  display: block;
}

.show-menu {
  transition: all .3s;
}

.show-menu .showm__box {
  box-shadow: 1.95px 1.95px 2.6px #00000026;
}

.show-menu .settings {
  opacity: 1;
}

#map {
  width: 100%;
  height: 100vh;
}

.cooldown {
  position: fixed;
  top: 15px;
  left: 50%;
  transform: translateX(-50%);
}

@media screen and (width >= 1264px) {
  .cooldown {
    top: 5px;
  }
}

.cooldown__bg {
  background: #e4e4e7cc;
  border: 2px dashed #000;
  border-radius: 3px;
  padding: 10px;
}

@media screen and (width >= 1264px) {
  .cooldown__bg {
    border: 1px dashed #000;
    padding: 10px;
  }
}

.cooldown__title {
  color: #000;
  font-family: Nata Sans;
  font-size: 16px;
  font-weight: 400;
}

@media screen and (width >= 752px) {
  .cooldown__title {
    font-size: 26px;
  }
}

@media screen and (width >= 1264px) {
  .cooldown__title {
    font-size: 26px;
  }
}

.showm {
  position: absolute;
  top: 15px;
  left: 10px;
}

@media screen and (width >= 1264px) {
  .showm {
    top: 5px;
    left: 10px;
  }
}

.showm__box {
  background: #e4e4e7cc;
  border: 1px dashed #000;
  border-radius: 3px;
  justify-content: center;
  align-items: center;
  width: 42px;
  height: 42px;
  transition: all .3s;
  display: flex;
}

@media screen and (width >= 752px) {
  .showm__box {
    width: 48px;
    height: 48px;
  }
}

.showm__button {
  background: none;
  font-size: 16px;
}

.settings {
  opacity: 0;
  transition: all .3s;
  position: absolute;
  top: 68px;
  left: 10px;
}

@media screen and (width >= 1264px) {
  .settings {
    top: 58px;
    left: 10px;
  }
}

.settings__box {
  background: #e4e4e7cc;
  border: 1px dashed #000;
  border-radius: 3px;
  justify-content: center;
  align-items: center;
  width: 42px;
  height: 42px;
  display: flex;
}

@media screen and (width >= 752px) {
  .settings__box {
    width: 48px;
    height: 48px;
  }
}

.settings__button {
  background: none;
}

.settings__svg {
  width: 18px;
  height: 18px;
}

.settings__overlay {
  pointer-events: none;
  background: #00000080;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

.settings__modal {
  background: #fff;
  border: 1px solid #ccc;
  border-radius: 3px;
  width: 70%;
  height: 80%;
  padding: 0 5px 5px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.settings__modal-title {
  margin-bottom: 15px;
  padding-left: 50px;
  font-family: Nata Sans;
  font-size: 25px;
}

.settings__modal-list {
  flex-direction: column;
  place-content: center;
  gap: 15px;
  padding-left: 50px;
  display: flex;
}

.settings__modal-item-subtitle {
  margin-bottom: 7px;
  font-family: Sansation;
  font-size: 20px;
}

.settings__modal-item-text {
  margin-bottom: 16px;
  font-family: Roboto Flex;
  font-size: 16px;
}

.settings__modal-item .setting__modal-item-line {
  background: #0000004d;
  width: 100%;
  height: 1px;
}

.cords {
  position: fixed;
  bottom: 15px;
  left: 15px;
}

@media screen and (width >= 1264px) {
  .cords {
    bottom: 10px;
    left: 10px;
  }
}

.cords__div {
  background-color: #e4e4e7cc;
  border: 2px dashed #000;
  border-radius: 3px;
  padding: 10px;
}

@media screen and (width >= 752px) {
  .cords__div {
    padding: 10px 20px;
  }
}

@media screen and (width >= 1264px) {
  .cords__div {
    padding: 10px 20px;
  }
}

.cords__text {
  cursor: pointer;
  font-family: Nata Sans;
  font-size: 18px;
}

@media screen and (width >= 752px) {
  .cords__text {
    font-size: 20px;
  }
}

.pallete {
  position: fixed;
  bottom: 15px;
  right: 15px;
}

.pallete__box {
  opacity: 1;
  background-color: #e4e4e7cc;
  border: 2px solid #000;
  border-radius: 5px;
  padding: 5px;
}

.pallete__list {
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  width: 140px;
  display: flex;
}

.pallete .color-btn {
  cursor: pointer;
  border: 2px solid #0000;
  border-radius: 4px;
  width: 25px;
  height: 25px;
  transition: border-color .2s, transform .1s;
}

.pallete .color-btn:hover {
  border-color: #666;
  transform: scale(1.1);
}

.pallete .color-btn.selected {
  border-color: #000;
}

.pallete__wrapper {
  justify-content: right;
  align-items: center;
  display: flex;
}

.pallete__button-open {
  border: 1px solid #000;
  border-radius: 2px;
  justify-content: center;
  align-items: center;
  width: 40px;
  height: 40px;
  margin-top: 10px;
  display: flex;
}

.pallete__button-open-svg {
  fill: #000;
  width: 18px;
  height: 18px;
}
